                    <div class="right-container">
                        <h3>BEKLEYEN TEHLİKE:</h3>
                        <h2>SIFIR GÜNÜ 25 LİTRE</h2>
                        <p>Güney Afrika’da yer alan Cape Town’da su kıtlığı olanca şiddetiyle devam ediyor.</p>
                        <p>2018 yılında bireysel kullanım için kişi başı yalnızca 25 litre su verilebilirdi!</p>
