                </div>
            </transition>
            <transition :key="storyIndex" enter-active-class="animated fadeIn delay-1s"
                        leave-active-class="animated fadeOut" mode="in-out">
                <div class="st-3-content-3" v-if="storyIndex === 4">
                    <Slider :slides="slides"/>
                    <div class="story-button-container" @click="nextSlide">
                        <div class="blue-button">
                            <img src="@/assets/img/icons/white-drop.svg"/>
                            <p>{{ slides[sliderIndex].button }}</p>
                        </div>
                    </div>
                </div>
            </transition>
        </div>
    </div>
</template>

<script>
    import ButtonBlueBackground from "../components/ButtonBlueBackground";
    import EventBus from "../main";
    import LineWithDots from "../components/LineWithDots";
    import Slider from "../components/Slider";
    import ChartIndicator from "../components/ChartIndicator";


    export default {
        name: "Story3",
        data() {
            return {
                storyIndex: 0,
                storyTotal: 4,
                storySequence: {
                    0: {
                        text: 'Son 15 Yılda Tüketim 4 kat arttı '
                    },
                    1: {
                        text: 'Türkiye’de nüfusun artması ve nüfusun şehirlerde birikmesi sonucu su seviyesinin\n' +
                            'şu an kritik olduğu şehirler. '
                    },
                    2: {
                        text: "2030 Yılında ülke nüfüsunun %65'i Su kıtlığı ile karşı karşıya kalacak"
                    }
                },
                chartIndicators: {
                    "istanbul": {
                        "container": `bottom: 299px; left: 121px; z-index: 2;`,
                        "line": "",
                        "textPos": "top: -15px; width: max-content; left: 45px;",
                        "text": "İstanbul / 2,84 milyar ton Su"
                    },
                    "izmir": {
                        "container": `bottom: 204px; left: 45px; z-index: 2;`,
                        "line": "transform: rotate(-180deg);",
                        "textPos": "top: -15px; width: max-content; left: -175px;",
                        "text": "İzmir / 1,22 milyar ton Su"
                    },
                    "ankara": {
                        "container": `left: 265px; bottom: 209px; z-index: 2; `,
                        "line": "",
                        "textPos": "top: -15px; width: max-content; left: 45px;",
                        "text": "Ankara / 1,84 milyar ton Su"
                    },
                    "adana": {
                        "container": `bottom: 76px; left: 330px; z-index: 2;`,
                        "line": `position: absolute; display: block; width: 70px; height: 1px; margin: 4px 0 4px 9.5px; background: white; transform-origin: -5px center; transform: rotate(90deg); `,
                        "textPos": `top: 75px; width: max-content; left: -75px;`,
                        "text": "Adana / 0,98 milyar ton Su"
                    }
                },
                storyInterval: null,
                sliderClass: false,
                slides: [
                    {
                        'header': '2015',
                        'image': 'story_3_images/group@2x.jpg',
                        'text': 'Cape Town su krizi 2015 yılında başladı. Belirli bölgelerde su kıtlığı baş gösterdi. 2018’in Nisan ayında “Day Zero (Sıfır Günü)” beklenmeye başladı. Bu şekilde, Cape Town Dünya’da suyu biten ilk büyük şehir olmaya aday  oldu.',
                        'button': '25 LİTRE İLE NE YAPILIR? '
                    },
                    {
                        'header': '25 Litre ile Ne Yapılır?',
                        'image': 'story_3_images/bitmap@3x.png',
                        'text': `<strong>TEMEL İHTİYAC</strong><br/>25 litre, Dünya Sağlık Örgütü'ne göre temel ihtiyaçların karşılanabilmesi için alt limittir.`,
                        'button': 'Ne yapabiliriz?'
                    }
                ],
                sliderIndex: 0,
                sliderDone: false,
                storyDone: false
            }
        },
        watch: {
            storyIndex(value) {
                if (value === 3) {
                    this.startThirdStory();
                    EventBus.$emit('story-background', 'hide-dune-video');
                } else if (value === 4) {
                    this.sliderClass = true;
                }
            },
            sliderIndex(value) {
                value === this.slides.length - 1 ? this.storyDone = true : this.storyDone = false;
            },
            storyDone(value) {
                value ? EventBus.$emit('story-done', {story: 3, value: true}) : EventBus.$emit('story-done', {
                    story: 3,
                    value: false
                });
            }
        },
        methods: {
            startStory() {
                EventBus.$emit('story-background', 'show-dune-video');
                this.$store.commit('changeStory', 3);
                this.storyInterval = setInterval(() => {
                    this.storyIndex < this.storyTotal - 1 ? this.storyIndex++ : clearInterval(this.storyInterval);
                }, 10000);
            },
            startThirdStory() {
                setTimeout(() => {
                    this.storyIndex++;
                }, 10000);
            },
            nextSlide() {
                if (this.sliderIndex === this.slides.length - 1) {
                    this.$router.push({name: 'Story4'});
                } else {
                    this.sliderIndex++;
                    EventBus.$emit('next-slide');
                }
            }
        },
        mounted() {
            EventBus.$emit('menu-status', false);
            this.$store.commit('navigationStatus', 'story');
            EventBus.$on('restart-story', () => {
                this.sliderDone = false;
                this.storyDone = false;
                this.storyIndex = 0;
                this.sliderIndex = 0;
                this.startStory();
            })
        },
        created() {
            setTimeout(this.startStory.bind(this), 2000);
        },
        beforeDestroy() {
            if (!(this.$route.name).includes('Story')) {
                EventBus.$emit('destroy-sequence');
            }
            EventBus.$emit('story-background', 'hide-dune-video');
            clearInterval(this.storyInterval);
        },
        components: {
            Slider,
            LineWithDots,
            ButtonBlueBackground,
            ChartIndicator
        }
    }
</script>

